import axios from "axios"
import { accountService } from "./account.service"
import router from "@/router"

// URL d'accs  mon serveur
const Axios = axios.create({
    baseURL: process.env.VUE_APP_API_URL
    // baseURL: 'https://back-8hi1.onrender.com'
})

// intercepte la requte et ajoute le token dans l'entte Authorization
Axios.interceptors.request.use(req => {
    if(accountService.isLogged){
        req.headers.Authorization = 'Bearer '+accountService.getToken()
    }
    return req
} )

// intercepte le token dans la rponse
Axios.interceptors.response.use(res => {
    return res
    }, error =>{
        if(error.response.status == 401 && error.response.message == "Non autoris"){
            accountService.logout()
            router.push('/')
        }
})

export default Axios