<template>
    <div>
        <GenericModal :showModal="showModal" @close="closeModal">
            <template #header>
                <h2>Modification du Produit</h2>
            </template>
            <!-- Le contenu principal utilise le slot par dfaut -->
            <form enctype='multipart/form-data' @submit.prevent="$event => editProduct()">
                <div class="banner-content">
                    <label for="imgProductEdit" class="label-changeFile">
                        <svg class="icon upload-icon">
                            <use href="@/assets/icons/icons.svg#upload"></use>
                        </svg>
                        <input class="changeFile" id="imgProductEdit" type="file" @change="$event => selectFile($event)"
                            accept="image/png, image/jpg, image/jpeg" />
                    </label>

                    <div class="picture">
                        <img v-if="this.product.imageUrl" class="oldImg" :class="{ 'newImg': this.product.newFile }"
                            :src="this.product.imageUrl">
                    </div>
                </div>

                <ul class="form-style-1">

                    <p v-if="errors.length">
                        <b>Corriger les erreurs suivantes</b>
                    <ul>
                        <li v-for="error in errors">{{ error }}</li>
                    </ul>
                    </p>

                    <li>
                        <label for="nom">Nom du Produit</label>
                        <input type="text" name="nom" class="field-divided" placeholder="Nom" v-model="product.nameProduct"
                            required />
                    </li>

                    <li>
                        <label for="description'">description</label>
                        <input type="text" name="description" class="field-long" placeholder="description"
                            v-model="product.descriptionProduct" required />
                    </li>

                    <li>
                        <label for="prix">Prix :</label>
                        <input type="number" name="prix" step="0.10" class="field-long" placeholder="en Euros"
                            pattern="^\d+(\.\d{1,2})?$" max="99.99" v-model="product.prixProduct" required>
                    </li>

                    <!-- <li>
                <input type='file' name='image' @change="filesChange($event.target.name, $event.target.files)">
                </li> -->

                    <li>
                        <label class="switch">
                            <input type="checkbox" v-model="this.product.available">
                            <span class="slider round"></span>
                        </label>
                        {{ product.available ? 'disponible' : 'indisponible' }}
                    </li>

                    <li>
                        <button type="submit" class="btn">MODIFIER</button>
                        <button type="button" class="btn delete" @click="$event => goDeleteProduct()">SUPPRIMER</button>
                    </li>
                        
                

                </ul>
            </form>

        </GenericModal>

    </div>
</template>
  
<script>
import GenericModal from './GenericModal.vue';
import { productService } from '@/services/product.service';

export default {
    components: {
        GenericModal
    },

    props:{
    productEdit: {
        type: Object,
        required: false,
        default: () => ({
            nameProduct: "",
            prixProduct: null,
            image: null,
            category: "",
            available: true,
            descriptionProduct: "",
        })
    }
},
    data() {
        return {
            showModal: false,
            product: {
                nameProduct: "",
                prixProduct: null,
                image: null,
                category: "",
                available: true,
                descriptionProduct: "",
            },
            errors: []
        }
    },
    watch: {
    productEdit: {
        immediate: true, // pour excuter le handler ds l'initialisation
        deep: true,     // pour surveiller les proprits imbriques de l'objet
        handler(newValue) {
            this.product = { ...newValue };
        }
    }
},

    methods: {

        openModal() {
        this.showModal = true;

      },

      closeModal() {
        this.showModal = false;
      },

        selectFile(event) {
            const file = event.target.files[0];
            this.product.image = event.target.files[0];
            if (file) {
                this.product.imageUrl = URL.createObjectURL(file);
                this.product.newFile = file;
            }
        },

        editProduct() {
            const formData = new FormData();
            formData.append('nameProduct', this.product.nameProduct);
            formData.append('prixProduct', this.product.prixProduct);
            formData.append('descriptionProduct', this.product.descriptionProduct);
            formData.append('image', this.product.image);
            formData.append('category', this.product.category);
            formData.append('available', this.product.available);
            productService.updateProduct(this.product._id, formData)
                .then(res => {
                    if (
                        res.data.modif.acknowledged == true
                        && res.data.modif.matchedCount == 1
                        && res.data.modif.modifiedCount == 0
                    ) {
                        console.log('aucune modif');
                    } else {
                        this.product.newFile = null
                        this.closeModal();


                      // mettre  jour le produit dans l'index
                      this.$emit('product-updated', this.product);
                    }
                })
                .catch(err => console.log(err))
        },

        goDeleteProduct() {
            productService.deleteProduct(this.product._id)
                .then(res => {
                    console.log(res.data);
                    this.closeModal();
                    this.$emit('product-deleted',this.product)
                })
                .catch(err => console.log(err))
        }


    },

}

</script>

<style scoped>

.banner-content {
    overflow: hidden;
    border-radius: 12px;
    position: relative;
    padding-top: 53.33%;
    border: 5px solid white;
}

.banner-content img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
}

.upload-icon {
    background-color: white;
    border-radius: 12px;
    width: 40px;
    height: 33px;
    padding: 5px;
    box-shadow: 0px 0px 5px 3px #00000030;
}

.label-changeFile {
    cursor: pointer;
    z-index: 1;
    position: absolute;
    translate: -50%, -50%;
    transform: translate(50%, -50%);
    top: 50%;
    right: 50%;
}

.changeFile {
    display: none;

}

.newImg {
    filter: brightness(0.7);
}

</style>
  